import { createContext, useState, useEffect, useContext } from 'react';
import axios from '../services/api'; // Assuming your axios instance is here
import { useRouter } from 'next/router';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const router = useRouter();

  // On initial load, try to retrieve user from localStorage
  useEffect(() => {
    try {
      const storedUser = localStorage.getItem('user');
      if (storedUser) {
        setUser(JSON.parse(storedUser));
      }
    } catch (error) {
      console.error("Failed to parse user from localStorage:", error);
      localStorage.removeItem('user'); // Clear potentially corrupted data
      localStorage.removeItem('token');
    }
  }, []); // Runs once on component mount

  // Login function
  const login = async (email, password) => {
    try {
      const res = await axios.post('/auth/login', { email, password });
      localStorage.setItem('token', res.data.token);
      localStorage.setItem('user', JSON.stringify(res.data.user)); // Store the user object
      setUser(res.data.user); // Update the state immediately
      router.push('/'); // Redirect to homepage
      return { success: true };
    } catch (err) {
      console.error('Login Failed:', err.response?.data || err.message);
      return { success: false, error: err.response?.data?.error || 'Login failed. Please check your credentials.' };
    }
  };

  // Register function
  const register = async (username, email, password) => {
    try {
      const res = await axios.post('/auth/register', { username, email, password });
      // For registration, we redirect to login, not auto-login
      router.push('/login');
      return { success: true };
    } catch (err) {
      console.error('Registration Failed:', err.response?.data || err.message);
      return { success: false, error: err.response?.data?.error || 'Registration failed. Please try again.' };
    }
  };

  // Logout function
  const logout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    setUser(null); // Clear the state immediately
    router.push('/'); // Redirect to homepage
  };

  // Provide the user state and auth functions to children components
  const authContextValue = {
    user,
    isAuthenticated: !!user, // Convenience prop to check if user is logged in
    login,
    register,
    logout,
  };

  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
};

// Custom hook to easily consume the AuthContext
export const useAuth = () => {
  return useContext(AuthContext);
};